<template>
  <div class="userUtil">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="line"></div>
        <div class="codeArea">

<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;button needlogin=true&gt;点击这里要登录&lt;/button&gt;
&lt;script type="text/javascript"&gt;
    import userUtil from "cy_static/userUtil";
&lt;/script&gt;
</xmp >

        </div>
        <div class="submitCode">
            <input type="button" class="btn-submit" value="点击运行代码" @click="runcode()">
        </div>
        <div class="iframeBox">
            <iframe ref="iframe" src="#/runcode"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">userUtil 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import userUtil from "cy_static/userUtil"; 
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;script type="text/javascript"&gt;
  import userUtil from "cy_static/userUtil"; 
  userUtil.checkLogin({
      url : "xxx.html",
      callback : function() {
      }
  });
&lt;/script&gt;
</xmp >

      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">对象方法</h2>				
            <ul class="ul-props">
              <li>
                <p><a>set</a>(token:String) : <a>Void</a></p>
                <p>设置登录token；会话中有效；</p>
              </li>
              <li>
                <p><a>get</a>() : <a>String</a></p>
                <p>获取登录token；</p>
              </li>
              <li>
                <p><a>check_v1</a>(callback:Function) : <a>Promise</a></p>
                <p>验证登录token是否有效；有效返回当前token否是返回""</p>
                <div class="showDetail">详细</div>
                <table class="table-props border">
                  <tr>
                    <th colspan="5">callback 参数</th>
                  </tr>
                  <tr>
                    <th style="width:20%">参数名</th>
                    <th style="width:20%">类型</th>
                    <th style="width:20%">默认值</th>
                    <th style="width:30%">描述</th>
                    <th style="width:10%" class="text-c">必需</th>
                  </tr>
                  <tr>
                    <td>callback</td>
                    <td class="atn">Function</td>
                    <td>null</td>
                    <td>回调函数：返回当前用户token</td>
                    <td class="text-c">N</td>
                  </tr>
                  <tr>
                    <td colspan="5">
<codemirror ref="demoCode1" class="demoCode" :options="cmOptions2"></codemirror>
<xmp ref="demoCodeVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    userUtil.check_v1(function() {
      // code
    });

    userUtil.check_v1().then(function() {
      // code
    });
&lt;/script&gt;
</xmp >
                  </td>
								</tr>
                </table>
              </li>
              <li>
                <p><a>checkLogin</a>(param:Object) : <a>Void</a></p>
                <p>验证用户登录状态下方可执行的行为操作；</p>
                <div class="showDetail">详细</div>
                <table class="table-props border">
                  <tr>
                    <th colspan="5">callback 参数</th>
                  </tr>
                  <tr>
                    <th style="width:20%">参数名</th>
                    <th style="width:20%">类型</th>
                    <th style="width:20%">默认值</th>
                    <th style="width:30%">描述</th>
                    <th style="width:10%" class="text-c">必需</th>
                  </tr>
									<tr>
										<td>url</td>
										<td>String</td>
										<td>""</td>
										<td>跳转URL：如果用户已经登录跳转到该URL；<br>没有登录调用APP登录或H5登录并跳转URL；</td>
                    <td class="text-c">N</td>
									</tr>
									<tr>
										<td>callback</td>
										<td>Function</td>
										<td>""</td>
										<td>回调函数：如果已经登录调用回调函数；（参数token, url）；<br>没有登录调出APP登录（触发回调）或H5登录（不触发回调）；</td>
                    <td class="text-c">N</td>
									</tr>
                  <tr>
                    <td colspan="5">
<codemirror ref="demoCode2" class="demoCode" :options="cmOptions2"></codemirror>
<xmp ref="demoCodeVal2" style="display:none">
&lt;script type="text/javascript"&gt;
    userUtil.checkLogin("xxx.html");

    userUtil.checkLogin(function() {
    });

    userUtil.checkLogin({
        url : "xxx.html",
        callback : function() {}
    });
&lt;/script&gt;
</xmp >
                  </td>
								</tr>
                </table>
              </li>
              <li>
                <p><a>checkLink</a>() : <a>Void</a></p>
                <p>验证a标签RUL是否要登录；</p>		
                <div class="showDetail">详细</div>
                <table class="table-props border">
                  <tr>
                    <td>
                      <p>
                        添加方式有2种形式:<br>
                        1、在a标签的href属性添加<span class="atn"> needlogin=true</span>；<br>
                        2、在a标签添加<span class="atn"> needlogin </span>属性；<br>
                        2、一个路由只需调用一次；
                      </p>
                    </td>
                  </tr>
                  <tr>
                    <td>
<codemirror ref="demoCode3" class="demoCode" :options="cmOptions2"></codemirror>
<xmp ref="demoCodeVal3" style="display:none">
&lt;a href="xxx.html?needlogin=true"&gt;我的钱包&lt;/a&gt;

&lt;a href="xxx.html" needlogin="true"&gt;我的钱包&lt;/a&gt;

&lt;script type="text/javascript"&gt;
    userUtil.checkLink();
&lt;/script&gt;
</xmp >
                    </td>
                  </tr>                  
                </table>				
              </li>
 
            </ul>
      </div>
 
    </div> 
  </div>
</template>

<script>
import {trim, showDetail} from "../../../assets/js/codeStyle";
export default {
  name: 'userUtil',
  data () {
    return {
      msg: 'userUtil',
      cmOptions:{
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true

      },
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      },
      cmOptions2:{
            readOnly:"true",
            theme : "mdn-like",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
    var m = this;  
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
    showDetail();
  }, 
  methods:{
    runcode(){
      var m = this, frame = m.$refs.iframe;
      frame.src = "?a=" + Math.random() + "#/runcode"
      frame.onload = function() {
          frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
      }

    }
  }
}
</script>  
<style scoped>
  .cy_example .codeArea{height:240px}
  .cy_example .iframeBox{flex:10}
</style>



